<template>
    <div id="app">
        <wv-button type="primary" @click="getOpenId">openId</wv-button>
        <wv-button type="primary" @click="getVersion"></wv-button>
        <wv-button type="primary" @click="showLoader">loader</wv-button>
        <wv-button type="primary" @click="showMessage"></wv-button>
        <wv-button type="primary" @click="fetchData">ajax</wv-button>
        <wv-button type="primary" @click="getLocationHashValue">Hash</wv-button>
        <wv-button type="primary" @click="updateLocationHashKey">Hashname</wv-button>
        <wv-button type="primary" @click="getQueryString"></wv-button>
        <wv-button type="primary" @click="setLocalStorage">localStoragenametcbj</wv-button>
        <wv-button type="primary" @click="getLocalStorage">localStoragename</wv-button>
        <wv-button type="primary" @click="setCookie">cookiemyName</wv-button>
        <wv-button type="primary" @click="getCookie">cookiemyName</wv-button>
    </div>
</template>

<script>
    export default {
        name: 'app',
        data() {
            return {}
        },
        mounted() {

            this.t.getOpenIdFromWx();

            alert('567');


        },
        methods: {

            getOpenId() {
                this.t.showMessage({
                    content: this.t.getOpenId()
                });
            },

            getVersion() {
                this.t.showMessage({
                    content: this.t.getVersion()
                });
            },
            showLoader() {
                console.log('showLoader');
                this.t.showLoader({
                    time: 2000
                });
            },

            showMessage() {
                console.log('showMessage');
                this.t.showMessage({
                    content: ''
                });
            },

            fetchData() {
                console.log('fetchData');

                let tempParams = {
                    openId: this.getOpenId(),
                    lang: 'zh_CN'
                };

                const API = this.t.getBaseURL() + '/archPocApi/getUserInfo';

                let tempObj = {
                    url: API,
                    method: 'get',
                    params: tempParams
                };

                this.t.ajax(tempObj).then(data => {
                    this.t.showMessage({
                        content: data.errorMessage
                    });
                }).catch(error => {
                    console.log(error);
                });
            },

            getLocationHashValue() {
                this.t.showMessage({
                    content: this.t.getLocationHashValue('name')
                });
            },

            getQueryString() {
                this.t.showMessage({
                    content: this.t.getQueryString('name')
                });
            },

            setLocalStorage() {
                this.t.localStorage('name', 'tcbj');
            },

            getLocalStorage() {
                this.t.showMessage({
                    content: this.t.localStorage('name')
                });
            },

            updateLocationHashKey() {
                let value = 'baiyue';
                this.t.updateLocationHashKey('name', value);
                console.log('locationHashKey ===' + value);
            },

            setCookie() {
                this.t.cookie('myName', 'huohuohuo');
            },

            getCookie() {
                this.t.showMessage({
                    content: this.t.cookie('myName')
                });
            }
        }
    }
</script>

<style>
    body {
        margin: 0;
        padding: 20px 10px;
    }
</style>


